export default {
    name: 'header',
    props:['ItemList'],
	data(){
		return{
			current:0,
			alist:this.ItemList,
			selected:'selected'
		}
	},
	computed:{
		navLength:function(){
			return 100/this.alist.length;
		}
	},
	methods: {
		navigateto(path,index){
			this.current = index;
			uni.navigateTo({
				url: path
			});
		}
	}
}
